'use client';

import React, { useState } from 'react';
import Link from 'next/link';
import { useRouter } from 'next/navigation';
import { useLanguage } from '@/context/LanguageContext';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Switch } from '@/components/ui/switch';
import { loginSchema } from '@/lib/utils';

export default function LoginPage() {
  const { language, toggleLanguage, t } = useLanguage();
  const router = useRouter();
  const [formData, setFormData] = useState({
    email: '',
    password: ''
  });
  const [errors, setErrors] = useState<Record<string, string>>({});
  const [isLoading, setIsLoading] = useState(false);
  const [loginError, setLoginError] = useState('');

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
    
    if (errors[name]) {
      setErrors(prev => ({ ...prev, [name]: '' }));
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setLoginError('');
    setErrors({});

    try {
      const validatedData = loginSchema.parse(formData);

      const response = await fetch('/api/auth/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(validatedData),
      });

      const result = await response.json();

      if (response.ok) {
        localStorage.setItem('tamizhravels-token', result.token);
        localStorage.setItem('tamizhravels-user', JSON.stringify(result.user));
        router.push('/dashboard');
      } else {
        setLoginError(result.error || 'Login failed');
      }
    } catch (error: any) {
      if (error.errors) {
        const fieldErrors: Record<string, string> = {};
        error.errors.forEach((err: any) => {
          fieldErrors[err.path[0]] = err.message;
        });
        setErrors(fieldErrors);
      } else {
        setLoginError('An unexpected error occurred');
      }
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-green-50 flex items-center justify-center p-4">
      <div className="w-full max-w-md">
        <div className="text-center mb-8">
          <Link href="/" className="inline-flex items-center space-x-3 mb-6">
            <div className="w-12 h-12 bg-blue-600 rounded-lg flex items-center justify-center">
              <span className="text-white font-bold text-xl">T</span>
            </div>
            <h1 className="text-3xl font-bold text-gray-900">
              {t('app.name')}
            </h1>
          </Link>
          
          <div className="flex items-center justify-center space-x-2 mb-4">
            <Label htmlFor="language-toggle" className="text-sm font-medium">
              {language === 'english' ? 'தமிழ்' : 'English'}
            </Label>
            <Switch
              id="language-toggle"
              checked={language === 'tamil'}
              onCheckedChange={toggleLanguage}
            />
          </div>
        </div>

        <Card className="shadow-lg">
          <CardHeader className="text-center">
            <CardTitle className="text-2xl font-bold">
              {t('auth.login')}
            </CardTitle>
            <CardDescription>
              {language === 'english' 
                ? 'Enter your credentials to access your account'
                : 'உங்கள் கணக்கை அணுக உங்கள் விவரங்களை உள்ளிடவும்'
              }
            </CardDescription>
          </CardHeader>
          
          <CardContent>
            {loginError && (
              <Alert className="mb-4 border-red-200 bg-red-50">
                <AlertDescription className="text-red-800">
                  {loginError}
                </AlertDescription>
              </Alert>
            )}

            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="email">{t('auth.email')}</Label>
                <Input
                  id="email"
                  name="email"
                  type="email"
                  placeholder={language === 'english' ? 'Enter your email' : 'உங்கள் மின்னஞ்சலை உள்ளிடவும்'}
                  value={formData.email}
                  onChange={handleInputChange}
                  className={errors.email ? 'border-red-500' : ''}
                />
                {errors.email && (
                  <p className="text-sm text-red-600">{errors.email}</p>
                )}
              </div>

              <div className="space-y-2">
                <Label htmlFor="password">{t('auth.password')}</Label>
                <Input
                  id="password"
                  name="password"
                  type="password"
                  placeholder={language === 'english' ? 'Enter your password' : 'உங்கள் கடவுச்சொல்லை உள்ளிடவும்'}
                  value={formData.password}
                  onChange={handleInputChange}
                  className={errors.password ? 'border-red-500' : ''}
                />
                {errors.password && (
                  <p className="text-sm text-red-600">{errors.password}</p>
                )}
              </div>

              <Button 
                type="submit" 
                className="w-full" 
                disabled={isLoading}
              >
                {isLoading ? t('common.loading') : t('auth.sign_in')}
              </Button>
            </form>

            <div className="mt-6 text-center">
              <p className="text-sm text-gray-600">
                {t('auth.dont_have_account')}{' '}
                <Link 
                  href="/register" 
                  className="text-blue-600 hover:text-blue-800 font-medium transition-colors"
                >
                  {t('auth.sign_up')}
                </Link>
              </p>
            </div>

            <div className="mt-6 p-4 bg-gray-50 rounded-lg">
              <h4 className="text-sm font-medium text-gray-700 mb-2">
                {language === 'english' ? 'Demo Credentials:' : 'டெமோ விவரங்கள்:'}
              </h4>
              <div className="text-xs text-gray-600 space-y-1">
                <p><strong>Email:</strong> user@tamizhravels.com</p>
                <p><strong>Password:</strong> password123</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <div className="mt-6 text-center">
          <Link 
            href="/admin/login"
            className="text-sm text-gray-600 hover:text-gray-800 transition-colors"
          >
            {language === 'english' ? 'Admin Login' : 'நிர்வாக உள்நுழைவு'}
          </Link>
        </div>
      </div>
    </div>
  );
}
